











import Vue from "vue";

import CardAnswer from "@/vue/components/card/CardAnswer.vue";
import CardBackDisplay from "@/vue/components/card/CardBackDisplay.vue";

export default Vue.extend({
  methods: {
    goToNextCard: function() {
      const lesson = this.$store.state.currentLesson;
      const router = this.$router;
      return function() {
        const card = lesson.moveToNextCard();
        if (card) {
          router.push("/card/front");
        } else {
          router.push("/lesson/review");
        }
      };
    }
  },
  data: function() {
    return {
      lesson: this.$store.state.currentLesson,
      card: this.$store.state.currentLesson.getCurrentReviewCard()
    };
  },
  components: {
    CardBackDisplay: CardBackDisplay,
    CardAnswer: CardAnswer
  }
});
