




















import Vue from "vue";

import { CardAnswer } from "@/lib/entity/card";
import { ReviewCard } from "@/lib/value-objects/lesson";

import { cardStorage } from "../../util/Repositories";

function ans(reviewCard: ReviewCard, answer: CardAnswer): void {
  reviewCard.answer(answer);
  cardStorage.setItem(reviewCard.card.id, reviewCard.card);
}

export default Vue.extend({
  name: "CardAnswer",
  data: function() {
    return {
      CardAnswer: CardAnswer
    };
  },
  props: {
    card: ReviewCard,
    callback: Function
  },
  methods: {
    answer: function(answer: CardAnswer) {
      const card = this.$props.card;
      ans(card, answer);
      this.$props.callback();
    }
  }
});
